"use client";

import { useEffect, useState } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import CategorySelector from "./components/CategorySelector";
import SmartSubQueryForm from "./components/SmartSubQueryForm";
import QueryPreview from "./components/QueryPreview";
import ResultView from "./components/ResultView";
import { sendQuery, fetchCatalog } from "../api/backend";

export default function QueryBuilderPage() {
  const [catalog, setCatalog] = useState<Record<string, any> | null>(null);
  const [category, setCategory] = useState<string | null>(null);
  const [typeName, setTypeName] = useState<string | null>(null);
  const [rows, setRows] = useState<any[]>([]); // each row: {problem, speed, notes}
  const [finalQuery, setFinalQuery] = useState("");
  const [result, setResult] = useState<any>(null);
  const [clausePreview, setClausePreview] = useState<string | null>(null);

  useEffect(() => {
    fetchCatalog().then((c) => setCatalog(c));
  }, []);

  useEffect(() => {
    // when user picks type, auto-populate first row with first problem if available
    if (category && typeName && catalog) {
      const problems = catalog[category]?.[typeName]?.problems || [];
      setRows([{ problem: problems[0] || "", speed: "", notes: "" }]);
      const standards = catalog[category]?.[typeName]?.standards || [];
      setClausePreview(standards.length ? standards.join(" ; ") : null);
    }
  }, [category, typeName, catalog]);

  function updateRow(idx: number, field: string, value: string) {
    const next = [...rows];
    next[idx][field] = value;
    setRows(next);
    buildFinalQuery(next);
  }

  function addRow() {
    setRows([...rows, { problem: "", speed: "", notes: "" }]);
  }
  function removeRow(idx: number) {
    const next = rows.filter((_, i) => i !== idx);
    setRows(next);
    buildFinalQuery(next);
  }

  function buildFinalQuery(rRows: any[]) {
    const q = rRows
      .map((r) => {
        const typeText = typeName ?? category ?? "";
        const problem = r.problem || "";
        const speed = r.speed ? ` at ${r.speed}` : "";
        const notes = r.notes ? ` (${r.notes})` : "";
        return `${typeText} — ${problem}${speed}${notes}`;
      })
      .join("; ");
    setFinalQuery(q);
  }

  async function handleSubmit() {
    // optional: refine using a small local template (or go straight to backend)
    const response = await sendQuery(finalQuery, 4);
    setResult(response.data);
  }

  if (!catalog) {
    return <div className="p-8">Loading catalog…</div>;
  }

  return (
    <div className="p-10 max-w-6xl mx-auto space-y-6">
      <Card className="shadow-lg">
        <CardHeader>
          <CardTitle>RoadMarshal AI — Smart Query Builder</CardTitle>
        </CardHeader>
        <CardContent className="space-y-6">
          <CategorySelector
            catalog={catalog}
            category={category}
            setCategory={(c: string) => { setCategory(c); setTypeName(null); }}
            typeName={typeName}
            setTypeName={setTypeName}
          />

          {category && typeName && (
            <>
              <div className="space-y-3">
                <h3 className="text-lg font-semibold">Quick clause preview</h3>
                <div className="text-sm text-zinc-700">
                  {clausePreview ?? "No clause info available for selected type."}
                </div>
              </div>

              <SmartSubQueryForm
                category={category}
                typeName={typeName}
                catalog={catalog}
                rows={rows}
                updateRow={updateRow}
                addRow={addRow}
                removeRow={removeRow}
              />

              <QueryPreview query={finalQuery} onSubmit={handleSubmit} />
            </>
          )}
        </CardContent>
      </Card>

      {result && (
        <Card className="shadow-md">
          <CardContent>
            <ResultView result={result} />
          </CardContent>
        </Card>
      )}
    </div>
  );
}
